.show-more {
  position: relative;
}

.show-more__wrapper {
  padding: 53px 20px 25px 21px;

  @media (min-width: 768px) {
    padding: 161px 37px 38px 38px;
    border: 2px solid #f2f2f2;
  }

  @media (min-width: 1440px) {
    padding: 159px 38px 41px 38px;
    margin-top: 8px;
  }
}

.show-more__wrapper::before {
  position: absolute;
  content: "";
  background-image: url("../img/show-more.svg");
  background-repeat: no-repeat;
  background-size: cover;
  top: 0;
  left: calc(50% - 15px);
  width: 30px;
  height: 30px;

  @media (min-width: 768px) {
    top: 53px;
    left: calc(50% - 29px);
    width: 60px;
    height: 60px;
  }
}

.show-more__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
  text-align: center;
  margin: 0;

  @media (min-width: 768px) {
    font-size: 20px;
    line-height: 22px;
  }
}

.show-more__title br {
  display: none;

  @media (min-width: 768px) {
    display: inline;
  }
}

.show-more__text {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  text-align: center;
  margin: 0;
  margin-top: 8px;
  margin-bottom: 21px;
  margin-left: -2px;

  @media (min-width: 768px) {
    font-size: 16px;
    line-height: 18px;
    margin-top: 23px;
    margin-bottom: 87px;
  }

  @media (min-width: 768px) {
    margin-top: 23px;
    margin-bottom: 87px;
  }
}

.show-more__text br {
  display: none;

  @media (min-width: 768px) {
    display: inline;
  }
}

.show-more__button {
  font-size: 16px;
  line-height: 20px;
  min-width: 280px;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #f2f2f2;
  color: #444444;
  margin-left: -21px;

  @media (min-width: 768px) {
    min-width: 248px;
    margin-left: -2px;
    margin-top: -1px;
  }

  @media (min-width: 1440px) {
    min-width: 168px;
  }
}

.show-more__button:hover {
  background-color: #ebebeb;
}

.show-more__button:active {
  background-color: #ebebeb;
  color: rgba(68, 68, 68, 0.3);
}
