.example-results__wrapper {
  @media (min-width: 768px) {
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: 1440px) {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.example-results {
  margin: 0;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  position: relative;

  @media (min-width: 768px) {
    margin-bottom: 0;
  }

  @media (min-width: 1440px) {
    margin-bottom: 55px;
    width: 366px;
  }
}

.example-results__item {
  width: 124px;

  @media (min-width: 768px) {
    width: 162px;
  }
}

.example-results__item--weight .example-results__description {
  margin-left: -3px;
}

.example-results__item--weight::before {
  position: absolute;
  content: "";
  width: 124px;
  height: 56px;
  top: -15px;
  left: 0;
  background-image: url("../img/example-border-mobile.svg");
  background-repeat: no-repeat;

  @media (min-width: 768px) {
    width: 162px;
    height: 72px;
    background-image: url("../img/example-border-tablet-weight.svg");
  }
}

.example-results__item--time::before {
  position: absolute;
  content: "";
  width: 124px;
  height: 56px;
  top: -15px;
  left: 156px;
  background-image: url("../img/example-border-mobile.svg");
  background-repeat: no-repeat;

  @media (min-width: 768px) {
    width: 162px;
    height: 72px;
    left: 203px;
    background-image: url("../img/example-border-tablet-time.svg");
  }
}

.example-results__item--time {
  @media (min-width: 768px) {
    margin-left: 42px;
  }
}

.example-results__number {
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 7px;

  @media (min-width: 768px) {
    font-size: 30px;
    line-height: 37px;
    margin-bottom: 11px;
  }
}

.example-results__description {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
  color: #444444;
  margin: 0;
  text-align: center;

  @media (min-width: 768px) {
    font-size: 14px;
    line-height: 14px;
  }
}

.example-results__description br {
  @media (min-width: 768px) {
    display: none;
  }
}

.example-results__sum {
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  text-align: center;

  @media (min-width: 768px) {
    font-size: 20px;
    line-height: 28px;
    width: 174px;
    text-align: right;
    margin-top: -7px;
    padding-right: 2px;
  }

  @media (min-width: 1440px) {
    width: 436px;
    text-align: left;
    margin-top: -3px;
    margin-left: 1px;
  }
}

.example-results__sum-additional {
  @media (min-width: 1440px) {
    margin-left: 61px;
  }
}
